import React from 'react';
import {Link} from 'react-router-dom';
const NavBar = () => (
<nav>
    <ul>
            <li>
                <Link to="/">Home</Link>
            </li>

            <li>
                <Link to="/aboutPage">About</Link>
            </li>

            <li>
                <Link to="/articleList">Articles List</Link>
            </li>

            {/* <li>
                <Link to="/articlePage">Article</Link>
            </li> */}
    </ul>
</nav>
);

export default NavBar;